.page {
  scroll-behavior: smooth;

  &__body {
    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform 1s cubic-bezier(0.84, 0.16, 0.46, 1.8);
    z-index: 1;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 140px;

    &--mob-margin {
      @include onMobile {
        margin-bottom: 100px;
      }
    }
  }

  &__section-title {
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    margin-bottom: 48px;

    &--first {
      margin-bottom: 0;
    }

    @include onTablet {
      font-size: 48px;
      line-height: 48px;
    }

    @include onDescktop {
      font-size: 48px;
      line-height: 48px;
    }
  }

  &__section-subtitle {
    align-items: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    padding-top: 15px;
    margin: 0 auto 48px;

    @include onTablet {
      max-width: 392px;
    }

    @include onDescktop {
      max-width: 325px;
    }
  }
}
